.filter {
  margin-top: 40px;
}
.filter__body {
  display: flex;
  align-items: baseline;
  gap: 40px;
}
@media (max-width: 1360px) {
  .filter__body {
    flex-direction: column;
  }
}
.filter__wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  position: relative;
  max-width: 860px;
}
@media (max-width: 1360px) {
  .filter__wrapper {
    max-width: none;
    max-width: initial;
  }
}
.filter__top {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}
@media (max-width: 800px) {
  .filter__top {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}
.filter__search {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 800px) {
  .filter__search {
    justify-content: center;
  }
}
.filter__top-left {
  width: 100%;
}
.filter__share-btn {
  border: none;
  background-color: transparent;
  background-color: initial;
}
.filter__search-btn {
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-180%, -50%);
  border: none;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  cursor: pointer;
}
.filter__search-wrapper {
  border-radius: 2px;
  border: 1px solid #d7d7d7;
  background: #fff;
  position: relative;
  width: 100%;
  max-width: 350px;
}
.filter__search-wrapper input {
  border: none;
  padding: 12px 20px;
  width: 100%;
}
.filter__search-wrapper input:focus-visible {
  outline: none;
}
.filter__top-right {
  display: flex;
  align-items: baseline;
  gap: 20px;
  width: 100%;
  justify-content: flex-end;
}
.filter__top-right span {
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 400;
  line-height: 180%;
  letter-spacing: 0.32px;
  white-space: nowrap;
}
@media (max-width: 800px) {
  .filter__top-right {
    justify-content: center;
  }
}
.filter__search-nothing {
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 600;
  line-height: 14.364px;
}
.filter__search-popup {
  background-color: #fff;
  z-index: 1000;
  width: 100.6%;
  position: absolute;
  top: 100%;
  left: -1px;
  border: 1px solid #d7d7d7;
}
.filter__search-popup--infilter {
  position: static;
  top: 0;
  left: 0;
  width: 100%;
  border: medium none currentColor;
  border: initial;
  border-top: 1px solid #d7d7d7;
}
.filter__search-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 20px;
  border-radius: 2px;
  gap: 10px;
  max-height: 155px;
  overflow-y: auto;
  /* Стили для полосы прокрутки */
  /* Стили для "бегунка" прокрутки */
  /* Стили для трека прокрутки (фона) */
}
.filter__search-list::-webkit-scrollbar {
  width: 10px;
  padding-right: 10px;
}
.filter__search-list::-webkit-scrollbar-thumb {
  background-color: #d7d7d7; /* Цвет бегунка */
  border-radius: 5px; /* Скругление углов бегунка */
  width: 6px;
  height: 15px;
}
.filter__search-list::-webkit-scrollbar-track {
  width: 4px;
  height: 114px;
  background: #f3f3f3;
}
.filter__search-list li button {
  width: 100%;
  border: none;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 600;
  line-height: 14.364px;
  cursor: pointer;
  text-align: left;
  transition: all 0.3s;
}
.filter__search-list li button:hover {
  color: #7481ff;
}

.filter-sidebar {
  display: flex;
  flex-direction: column;
  gap: 31px;
  max-width: 300px;
  width: 100%;
}
@media (max-width: 1360px) {
  .filter-sidebar {
    max-width: none;
    max-width: initial;
  }
}
.filter-sidebar__top {
  display: flex;
  align-items: center;
  gap: 20px;
}
.filter-sidebar__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.filter-sidebar__clear {
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  border: none;
  color: #7481ff;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 500;
  line-height: 159%;
  cursor: pointer;
}

.recommendations__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.recommendations__list li button {
  border: none;
  background-color: transparent;
  background-color: initial;
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 500;
  line-height: 159%;
  cursor: pointer;
  width: 100%;
  text-align: left;
  transition: all 0.3s;
}
.recommendations__list li button:hover {
  color: #7481ff;
}

.selected-filters {
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}

.all-products {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: initial;
}
@media (max-width: 1368px) {
  .all-products {
    justify-content: center;
  }
}

.error {
  border-radius: 5px;
  background: #fff;
  background: var(--Primary-White, #fff);
  box-shadow: 0px 20px 25px 0px rgba(0, 0, 0, 0.1);
  padding: 60px;
  margin-top: 5px;
}
@media (max-width: 567px) {
  .error {
    padding: 30px;
  }
}
.error__body {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.error__title {
  color: #41456b;
  color: var(--Primary-Content, #41456b);
  font-family: Gilroy;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 25px;
}
@media (max-width: 567px) {
  .error__title {
    font-size: 16px;
  }
}
.error__subtitle {
  color: #41456b;
  color: var(--Primary-Content, #41456b);
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 180%; /* 28.8px */
  letter-spacing: 0.32px;
}
@media (max-width: 567px) {
  .error__subtitle {
    font-size: 14px;
  }
}
.error__clear {
  background: none;
  border: none;
  color: #7481ff;
  color: var(--Primary-Accent, #7481ff);
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 14.364px; /* 89.775% */
  letter-spacing: 0.32px;
  text-transform: uppercase;
  align-self: self-start;
  cursor: pointer;
}
@media (max-width: 567px) {
  .error__clear {
    font-size: 14px;
  }
