  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}
.make__title {
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 14.364px; /* 89.775% */
}
.make__wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.type {
  margin-top: 12px;
}
.type__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
}
@media (min-width: 1280px) {
  .type__list-item:hover .type__list-item-wrapper span {
    color: #7481ff;
  }
}
.type__list-item-check {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 500;
  line-height: 159%;
}
.type__list-item-wrapper {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 5px;
}
.type__list-item-wrapper span {
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 600;
  line-height: 159%;
  transition: all 0.3s;
}
.type__list-item-wrapper img {
  width: 34px;
  height: 15.622px;
}

.transmission {
  margin-top: 10px;
}
.transmission__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
}
@media (min-width: 1280px) {
  .transmission__list-item:hover .transmission__list-item-wrapper span {
    color: #7481ff;
  }
}
.transmission__list-item-check {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  color: #41456b;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 500;
  line-height: 159%; /* 25.44px */
}
.transmission__list-item-wrapper {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 5px;
}
.transmission__list-item-wrapper span {
  color: #41456b;
  color: var(--primary-content, #41456b);
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 14.364px;
  transition: all 0.3s;
}
.transmission__list-item-wrapper img {
  width: 34px;
  height: 15.622px;
}

.price {
  margin-top: 10px;
}
.price__counts {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price__counts span {
  color: #41456b;
  font-family: Gilroy;
  font-size: 20px;
  font-weight: 600;
  line-height: 25px; /* 125% */
