  max-width: 420px;
  width: 100%;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.car-product__body {
  display: flex;
  flex-direction: column;
}
.car-product__top img {
  max-width: 420px;
  width: 100%;
  height: 216px;
  -o-object-fit: cover;
     object-fit: cover;
}
@media (max-width: 800px) {
  .car-product__top img {
    height: auto;
  }
}
.car-product__bottom {
  border-radius: 0px 0px 5px 5px;
  background: #fff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 567px) {
  .car-product__bottom {
    gap: 14px;
  }
}
.car-product__main-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.car-product__title {
  color: #41456b;
  font-family: Gilroy;
  font-size: 20px;
  font-weight: 600;
  line-height: 25px;
}
@media (max-width: 567px) {
  .car-product__title {
    font-size: 18px;
  }
}
.car-product__price {
  color: #7481ff;
  font-family: Gilroy;
  font-size: 20px;
  font-weight: 600;
  line-height: 25px;
}
@media (max-width: 567px) {
  .car-product__price {
    font-size: 18px;
  }
}
.car-product__secondary-info-list {
  list-style-type: none;
  display: flex;
  align-items: center;
}
.car-product__secondary-info-list li {
  color: #41456b;
  font-size: 16px;
  font-weight: 400;
  line-height: 180%;
  letter-spacing: 0.32px;
}
.car-product__secondary-info-list li:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 567px) {
  .car-product__secondary-info-list li:not(:last-child) {
    margin-right: 10px;
  }
}
@media (max-width: 567px) {
  .car-product__secondary-info-list {
    flex-wrap: wrap;
  }
